import axios from "../../API/axios";
import React, { useState } from "react";
import { Modal, Button, Form, FloatingLabel } from "react-bootstrap";
import { ReactSession } from "react-client-session";
import FetchUnits from "../../Data/API/FetchUnits";
import AddJob from "../AddJob";

function NewJobModal() {
  const [show, setShow] = useState(false);
  const [name, setName] = useState("");
  const [unit, setUnit] = useState("");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [state, setState] = useState("");
  const [creator, setCreator] = useState("");
  const user = ReactSession.get("username");

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const newJob = () => {
    //itt kell bekldeni POST-al a szervernek az adatok felvitelt!
    if (!user) {
      alert("no no no");
    }
    setCreator(user);
    console.log(
      `${name} | ${unit} | ${startDate} | ${endDate} | ${state} | ${creator}`
    );
    let job = {
      unitId: unit,
      jobName: name,
      startDate: startDate,
      jobStatus: state,
      endDate: endDate,
      createdBy: user,
      comment: ""
    };
    axios.post("jobs/add", job).then(() => alert("Done"));
    setShow(false);
  };

  return (
    <>
      <Button variant="outline-success" onClick={handleShow}>
        j keress
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>j keress ltrehozsa</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <FloatingLabel label="Megnevezs" className="mb-3">
            <Form.Control
              placeholder="Megnevezs"
              onChange={(e) => setName(e.target.value)}
            ></Form.Control>
          </FloatingLabel>

          <FloatingLabel label="Szervezeti egysg" className="mb-3">
            <Form.Select
              placeholder="Szervezeti egysg"
              onChange={(e) => setUnit(e.target.value)}
            >
              <FetchUnits />
            </Form.Select>
          </FloatingLabel>

          <FloatingLabel label="Kezds" className="mb-3">
            <Form.Control
              placeholder="Kezds"
              onChange={(e) => setStartDate(e.target.value)}
            ></Form.Control>
          </FloatingLabel>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="outline-danger" onClick={handleClose}>
            Mgsem
          </Button>
          <Button variant="success" onClick={() => newJob()}>
            Ltrehozs
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
export default NewJobModal;
