import React, { useState } from "react";
import { Modal, Button, Form, Table } from "react-bootstrap";
import axios from "../../API/axios";
import swal from "sweetalert";
import party from "party-js";

function JobDoneModal(props) {
  const [show, setShow] = useState(false);
  const [comment, setComment] = useState("");

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const jobDone = (runButton) => {
    //Itt kell posttal kldeni az api fel a ltrehozst !
    //console.log(`Unitname: ${unitName}`)
    party.confetti(runButton, {
      count: party.variation.range(20, 40),
      size: party.variation.range(1.5 , 2)
    });
    let unit = { jobId: props.jobId, comment: comment };
    axios
      .post("jobs/done", unit)
      .then(() => {
        setShow(false);
        swal("Sikeres!", "Sikeresen kszre lltotad a keresst! ", "success");
        props.refresh();
      })
      .catch((err) => swal(`A kvetkez hiba trtnt! : ${err}`));
  };

  return (
    <>
      <Button variant="" className="p-0" onClick={handleShow}>
        Kszre jelent
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Adj hozz megjegyzst!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Label>Megjegyzs</Form.Label>
          <Form.Control
            onChange={(e) => setComment(e.target.value)}
          ></Form.Control>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="outline-danger" onClick={handleClose}>
            Mgsem
          </Button>
          <Button variant="success" onClick={(e) => jobDone(e.target)}>
            Kszre jelent
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
export default JobDoneModal;
